body {
  background-color: #ffffff;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #ececec;
}

* {
  font-family: Arial, Helvetica, sans-serif;
}

.top-banner {
  background-color: #323635;
  height: 50px;
}

.dropleft.nav-item a {
  color: #fff !important;
}

.settings-dropdown {
  min-width: 300px;
  width: 300px;
}

.settings-dropdown-item {
  min-width: 300px;
  width: 300px;
  padding: 0;
  margin: 0;
}

.header-row {
  background-color: transparent;
  min-height: 60vh;
}

.app-header {
  min-height: 60vh;
  padding-top: 7vh;
  color: #323635;
  height: 100%;
  background-image: -webkit-linear-gradient(
    top left,
    rgb(149, 209, 191),
    #e4b2cb
  );
  background-image: linear-gradient(
    to bottom right,
    rgb(149, 209, 191),
    #e4b2cb
  );
  background-size: 120% 120%;
}

.app-header-content {
  height: 45vh;
}

.bottom-banner {
  background-color: transparent;
  text-align: center;
  min-height: 10%;
  height: 10%;
}

.app-title-small {
  color: #fff;
  font-size: larger;
}

.app-title {
  background-color: #fff;
  text-align: center;
  border-radius: 50%;
  display: inline-block;
  width: 300px;
  height: 300px;
  mix-blend-mode: screen;
  color: #000;
}

.app-title-n {
  font-size: 4cm;
  padding: 0px;
  margin: 20px 0px 15px 0px;
}

.app-title-gram {
  font-size: 17mm;
  letter-spacing: 6px;
  line-height: 0px;
  padding: 0px;
  margin: 0px;
  margin-left: 2mm;
}

.app-subtitle {
  color: #fff;
  font-size: 30px;
  margin-top: 20px;
  text-align: center;
}

.corpus-container {
  color: white;
  height: 75%;
}

.result-row {
  min-height: 40vh;
  padding-top: 1cm;
  padding-bottom: 1cm;
  background-color: #e3e3e3;
}

.footer-row {
  min-height: 20vh;
  background-color: #5c5c5c;
}

.footer-text {
  text-align: center;
  padding-top: 1cm;
  padding-bottom: 1cm;
}

.footer-text p,
h2 {
  color: rgb(240, 240, 240);
}

.corpus-text {
  color: #fff;
  border-style: none;
  font-size: 18px;
  text-align: left;
  padding: 0;
  margin: 0;
  background: transparent;
  resize: none;
}

::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #fff;
}
::-moz-placeholder {
  /* Firefox 19+ */
  color: #fff;
}

.corpus-text:focus,
input:focus {
  outline: none;
}

.button-generate {
  background-color: #fff;
  color: #000;
  font-weight: bold;
  font-size: 20px;
  padding: 5px 20px 4px 20px;
  border-radius: 10px;
  mix-blend-mode: screen;
  border-style: none;
  widows: 100%;
  height: 100%;
}

.button-generate:hover {
  cursor: pointer;
  background-color: rgb(149, 209, 191);
}

.listItem {
  list-style: none;
  text-align: center;
  padding: 4px 6px 4px 6px;
  margin: 2px;
  border: none;
  background-color: #fff;
  border-radius: 8px;
}

.listItem:hover {
  cursor: pointer;
}

.list {
  padding: 0px;
  list-style: none;
  margin: 0px;
}

.list-container {
  margin: 10px 0px 10px 0px;
}

.settings-slider-container {
  padding-right: 0px !important;
  padding-left: 0px !important;
}

.settings-slider {
  -webkit-appearance: none;
  margin: 10px 5px 10px 5px;
  background: #dddddd;
  width: 100%;
  height: 20px;
  border-radius: 8px;
  margin: 0;
  padding: 0;
}

.settings-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  background: rgb(149, 209, 191);
  cursor: pointer;
  border-radius: 8px;
}

.settings-slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  background: rgb(149, 209, 191);
  cursor: pointer;
  border-radius: 8px;
}

.settings-slider:hover {
  cursor: pointer;
}

.settings-label {
  color: rgb(56, 56, 56);
  text-align: left;
}

